
.incomeWrap   .asgagewgf h3{
  font-weight: 500;
  line-height: 50px;
  color: #20a0ff;
}
.incomeWrap .el-select .el-input{
  width: 120px;
}
.incomeWrap   .asgagewgf .personalInfor{
  position: relative;
}
.incomeWrap   .asgagewgf .personalInfor img{
  position: absolute;
  top: 0;
  right: 0;
  width: 150px;
  height: 150px;
}
.incomeWrap   .asgagewgf  span{
  width: 30%;
  display: inline-block;
  line-height: 35px;
}
.incomeWrap .asgagewgf .ordertable span{
  display: inline-block;
  width: auto;
}
.incomeWrap .asgagewgf .ordertable td{
  line-height: 30px;
  padding-left: 10px;
}
.incomeWrap .asgagewgf .ordertable .maniCont{
  text-align: center;
}
.incomeWrap .asgagewgf .ordertable  .lastTr{
  color: red;
}
.incomeWrap .asgagewgf .ordertable  .lastTr td:nth-child(2){
  text-align: right;
  padding-right: 10px;
}
.datail_tb{
  width: 100%;
  border-collapse: collapse;/* 边框合并属性  */
  border: none;
  margin-top: 20px;
}
.datail_tb tr{
  width: 100%;
  height: 30px;
  line-height: 30px;
  border: 1px solid #ccc;
}
.datail_tb td{
  width: 100px;
  text-align: center;
  border: 1px solid #ccc;
}



