
.clearfix{
  zoom:1;
}
.clearfix:after{
  content:"";
  clear:both;
  display:block;
}
.brandWarp .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}
.brandWarp   .avatar-uploader .el-upload:hover {
    border-color: #20a0ff;
}
.brandWarp  .avatar-uploader-icon {
    font-size: 28px;
    width: 178px;
    height: 178px;
    line-height: 178px;
}
.brandWarp   .avatar {
    width: 178px;
    height: 178px;
    display: block;
}
.add_btn{
  float:right;
  margin: 20px 118px 20px 0;
}
.item_w_input{
  width: 216px;
}



