
.brandWarp{
    width: 1200px;
    margin: auto;
}
.fl{
    float:left;
}
.fr{
    float: right;
}
.clearfix{
    zoom:1;
}
.clearfix:after{
    content:"";
    clear:both;
    display:block;
}
.i_col_red{
    color: red;
    font-weight: bold;
    font-style: normal;
}
.margin_l{
    margin-left: 20px;
}
.total_box{
    width: 300px;
    height: 44px;
    font-size: 14px;
    line-height: 44px;
    background: #5db7e8;
    color: #fff;
    text-align: center;
}
.warp-breadcrum{
    margin-top: 36px;
}
.pad_l_30{
    padding-left: 30px;
}
.custInfo{
    height: 30px;
    line-height: 30px;
    padding-left: 24px;
    border-bottom: 1px dotted #ccc;
    background: url(../../../images/salesman/arrow.png) 0 8px no-repeat;
}
.detail_title{
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 16px;
    background: url(../../../images/salesman/titlebg.png) 400px 34px no-repeat;
}
.datail_tb{
    width: 100%;
    border-collapse: collapse;/* 边框合并属性  */
    border: none;
    margin-top: 20px;
}
.custContent{
    height: 70px;
    line-height: 70px;
    padding-left: 52px;
}
.datail_tb tr{
    width: 100%;
    height: 30px;
    line-height: 30px;
    border: 1px solid #ccc;
}
.trs td{
    width: 100px;
    text-align: center;
    border: 1px solid #ccc;
}



