
.brandWarp{
    width: 1200px;
    margin: auto;
}
.fl{
    float:left;
}
.fr{
    float: right;
}
.clearfix{
    zoom:1;
}
.clearfix:after{
    content:"";
    clear:both;
    display:block;
}
.i_col{
    color: #fff;
    font-size: 16px;
    font-weight: bold;
    font-style: normal;
}
.total_box{
    width: 300px;
    height: 44px;
    font-size: 14px;
    line-height: 44px;
    background: #5db7e8;
    color: #fff;
    text-align: center;
}
.warp-breadcrum{
    margin-top: 36px;
}



