
.brandWarp{
  width: 1200px;
  margin: auto;
}
.fl{
  float:left;
}
.fr{
  float: right;
}
.clearfix{
  zoom:1;
}
.clearfix:after{
  content:"";
  clear:both;
  display:block;
}
.i_col_red{
  color: red;
  font-weight: bold;
  font-style: normal;
}
.col_red{
  color: red;
}
.i_col_blue{
  color: #20a0ff;
  font-weight: bold;
  font-style: normal;
}
.col_blue{
  color: #20a0ff;
}
.margin_l{
  margin-left: 20px;
}
.warp-breadcrum1{
  margin-top: 36px;
}
.wallet_title{
  margin-top: 30px;
  margin-bottom: 8px;
}
.curOrder{
  height: 30px;
  line-height: 30px;
  margin-top: 30px;
  padding-left: 24px;
  border-bottom: 1px dotted #ccc;
  background: url(../../../images/salesman/arrow.png) 0 8px no-repeat;
}
.nav_select{
  width: 60px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  display: inline-block;
  border-radius: 6px;
  cursor: pointer;
}
.margin_r_30{
  margin-right: 30px;
}
.active_nav{
  background: #56bff8;
  color: #fff;
}
.btn_col{
  width: 120px;
  height: 36px;
  border-radius: 4px;
  cursor: pointer;
  background: #5db7e8;
  border: none;
  color: #fff;
}
.btn_col:hover{
  background: #57a5cf;
  border: none;
  color: #fff;
}
.sel_wrap{
  border: 1px solid #eee;
  margin-top: 26px;
  padding-left: 10px;
  padding-bottom: 20px;
}
.sel_wrap li{
  margin-top: 20px;
}
.item_w_input{
  width: 350px;
}
.item_c_input{
  width: 210px;
  margin-right: 20px;
}
.i_red{
  color: red;
}
.i_title{
  height: 30px;
  line-height: 30px;
}
.withDrawBtn{
  width: 140px;
  margin-left: 82px;
}
.withDrawBtn1{
  width: 140px;
  margin-left: 66px;
}



